<template>
  <div class="todo-header">
    <button class="todo-header-btn" @click="createNewOne">新建</button>
    <input v-model="keyword" class="todo-header-inp" type="text" placeholder="按姓名关键字搜索" @change='search' />
    <button class="todo-header-btn back" @click="clearkw">撤销</button>
  </div>
</template>

<script>
import { ref } from 'vue';
import { useStore } from 'vuex';
export default {
  name: "HeaDer",
  setup(props,{emit}){
    const store=useStore()
    const createNewOne=()=>{
      store.commit('list/changeShowDlg',true)
      emit('createNewOne')
    }

    const keyword=ref('')
    let itemList=store.state['list'].infoList
    let count=0
    const search=()=>{
      count=0
      itemList=[]
      store.state['list'].infoList.forEach(item => {
        if(item.name.indexOf(keyword.value)!=-1){
          itemList.push(item)
          store.commit('list/searchInfolist',itemList)
        }else{
          count++
        }
      })
      if(count===store.getters['list/list'].length){
        store.commit('list/searchInfolist',[])
      }
    }
    const clearkw=()=>{
      keyword.value=''
      search()
    }
    return{createNewOne,search,keyword,itemList,clearkw} 
  }
};
</script>

<style scoped>
/* header */
.todo-header {
  height: 20px;
  margin-bottom: 10px;
}
.todo-header-btn {
  font-size: 12px;
  outline: none;
  border: 0;
  padding: 2px 15px;
  line-height: 20px;
  margin-right: 10px;
  border-radius: 4px;
}
.todo-header-inp {
  width: 200px;
  display: inline-block;
  font-size: 12px;
  outline: none;
  border: 0;
  padding: 2px 10px;
  line-height: 20px;
  border-radius: 4px;
  background-color: rgb(239, 239, 239);
}
.todo-header .back {
  float: right;
  margin-right: 0;
}
.todo-header-inp::placeholder {
  color: black;
}
</style>
