<template>
  <div class="todo-main-body">
    <ul v-for="item in $store.getters['list/searchList']" :key="item.id">
      <li class="todo-main-body-list1">
        <input type="checkbox" :checked="item.ischeck" @change='change(item)'/>
        <span>{{item.name}}</span>
      </li>
      <li class="todo-main-body-list2">
        <span>{{item.age}}</span>
      </li>
      <li class="todo-main-body-list3">
        <span>{{item.gender}}</span>
      </li>
      <li class="todo-main-body-list4">
        <span>{{item.number}}</span>
      </li>
      <li class="todo-main-body-list5">
        <span>{{item.address}}</span>
      </li>
      <li class="todo-main-body-list6">
        <button @click="edit(item)">编辑</button>
        <button @click="deleteItem(item)">删除</button>
      </li>
    </ul>
