</template>

<script>
import {useStore} from 'vuex'
export default {
    name:'ListItem',
    setup(){
      const cancel=()=>{
        store.commit('list/changeShowDlg',false)
      }

      const store=useStore()
      const list=store.state.list.infoList
      const deleteItem=(item)=>{
        store.dispatch('list/deleteItem',item)
      }
      const edit=(item)=>{
        store.commit('list/changeShowDlg',true)
        store.commit('list/addTodo',item)
      }
      const change=(item)=>{
        store.commit('list/changeCheck',item)
      }
      return{list,deleteItem,cancel,edit,change}
    }
}
</script>

