<template>
  <div class="todo-footer">
    <button class="deleteAll" @click="deleteAll">批量删除</button>
  </div>
</template>

<script>
import { useStore } from 'vuex';
export default {
  name: "FooTer",
  setup(){
    const store=useStore()
    const deleteAll=()=>{
      store.commit('list/deleteAllCheck')
    }
    return{deleteAll}
  }
};
</script>

<style scoped>
/* footer */
.todo-footer {
  margin-top: 15px;
}
.deleteAll {
  font-size: 10px;
  outline: none;
  border: 0;
  padding: 2px 8px;
  line-height: 20px;
  margin-right: 2px;
  border-radius: 4px;
}
</style>
