<template>
<transition name="dialog-fade">
    <div class="wrap">
        <div class="container">
            <h3 class="title">新建/编辑用户</h3>
            <div class="body">
                <div class="body-item">
                    <h3>姓名</h3>
                    <input v-model="userInfo.name" type="text">
                </div>
                <div class="body-item">
                    <h3>年龄</h3>
                    <input v-model="userInfo.age" type="text">
                </div>
                <div class="body-item">
                    <h3>性别</h3>
                    <tSelect :options='selOptions' placeholder='请选择' v-model="userInfo.gender"></tSelect>
                </div>
                <div class="body-item">
                    <h3>联系电话</h3>
                    <input v-model="userInfo.number" type="text">
                </div>
                <div class="body-item">
                    <h3>详细地址</h3>
                    <input v-model="userInfo.address" type="text">
                </div>
            </div>
            <div class="btn">
                <button class="cancel-btn" @click="cancel">取消</button>
                <button class="submit-btn" @click="submit">保存</button>
            </div>
        </div>
    </div>
</transition>
</template>

<script>
import { reactive, watch } from 'vue'
import { useStore } from 'vuex'
export default {
    name:'DiaLog',
    props:{
        todo:{
            type:Object,
            default:()=>({})
        },
        modelValue:{
            type:Boolean,
            default:false
        }
    },
    setup(props,{emit}){
        const store=useStore()
        let userInfo=reactive({
            gender:'',
            name:'',
            age:'',
            number:'',
            address:''
        })
        const state=reactive({
        selOptions:[
            { label: "男", value: "man" },
            { label: "女", value: "woman"}
        ]
        })
        const { selOptions } = state;
        
        const cancel=()=>{
            emit('cancelCreate')
            emit('update:modelValue',false)
        }
        const submit=()=>{
            store.commit('list/changeShowDlg',false)
            if(userInfo.gender!==''&&userInfo.name!==''&&userInfo.age!==''&&userInfo.number!==''&&userInfo.address!==''){
                store.dispatch('list/addNewItem',userInfo)
            }else{
                alert('数值不能为空')
            }
            emit('update:modelValue',false)
        }
        watch(()=>props.todo,(newVal)=>{
            if(props.todo&&props.todo!==null){
                // eslint-disable-next-line no-const-assign
                userInfo=Object.assign(userInfo,newVal)
            }
        },{immediate:true})
        watch(()=>props.modelValue,()=>{
           if(props.modelValue){
                userInfo={
                gender:'',
                name:'',
                age:'',
                number:'',
                address:''
            }
           }
        },{immediate:true})

        return{userInfo,selOptions,cancel,submit}
    }
}
</script>

<style scoped>
.wrap{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.1);
}
h3{
    margin: 0 0 10px 0;
}
.container{
    width: 550px;
    height: 450px;
    background: #fff;
    margin: 50px auto;
    border-radius: 4px;
    padding: 15px;
    position: relative;
}
.title{
    height: 25px;
    line-height: 25px;
    text-align: center;
    margin-bottom: 30px;
}
.body{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.body input{
    width: 239px;
    height: 22px;
    outline: none;
    border: 1px solid #f0f0f0;
    padding: 4px 5px;
    /* box-sizing: border-box; */
}
.body-item{
    margin-bottom: 30px;
}
.body-item:last-child input{
    width: 400px;
}
.cancel-btn{
    border: 1px solid #f0f0f0;
    background-color: #f0f0f0;
    border-radius: 3px;
    cursor: pointer;
    padding: 5px 15px;
    margin-right: 15px;
}
.btn{
    position: absolute;
    right: 40px;
    bottom: 30px;
}
.submit-btn{
    border: 1px solid skyblue;
    background-color: skyblue;
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    padding: 5px 15px;  
}

.dialog-fade-enter-active{
    animation: fade 0.3s;
  }
  .dialog-fade-leave-active{
    animation: fade 0.2s reverse;
  }
  @keyframes fade{
    0% {
      opacity: 0;
      transform: translateY(-20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }
</style>