import { nanoid } from 'nanoid'
export default {
    namespaced:true,
    state(){
        return{
            // infoList:[
            //     {id:'001',name:'xiaomi',age:'18',gender:'man',number:'135555555',address:'dgaig',ischeck:false},
            //     {id:'002',name:'chenmi',age:'58',gender:'man',number:'157777777',address:'dgaig',ischeck:false},
            //     {id:'003',name:'miqi',age:'63',gender:'woman',number:'178888888',address:'twvfiwv',ischeck:true},
            // ],
            infoList:JSON.parse(localStorage.getItem('infoList')) || [],
            showDialog:false,
            userInfo:{
                id:'',
                gender:'',
                name:'',
                age:'',
                call:'',
                address:''
            },
            todo:null,
            searchList:JSON.parse(localStorage.getItem('infoList')) || []
        }
    },
    mutations:{
        deleteItem(state,item){
            state.infoList=state.infoList.filter(x=>x.id!==item.id)
            localStorage.setItem('infoList',JSON.stringify(state.infoList))
            state.searchList=state.infoList
        },
        addNewItem(state,item){
            if(item.id){
                let child=state.infoList.find(x=>x.id===item.id)
                // eslint-disable-next-line no-unused-vars
                child=Object.assign(child,item)
            }else{
                const newList={id:nanoid(),...item,ischeck:false}
                state.infoList.push(newList)
                localStorage.setItem('infoList',JSON.stringify(state.infoList))
                state.searchList=state.infoList
            }
            
        },
        changeShowDlg(state,val){
            state.showDialog=val
        },
        addTodo(state,val){
            const todo=state.infoList.find(x=>x.id===val.id)
            state.todo=todo
        },
        changeCheck(state,val){
            const todo=state.infoList.find(x=>x.id===val.id)
            todo.ischeck=!todo.ischeck
        },
        deleteAllCheck(state){
            state.infoList=state.infoList.filter(x=>x.ischeck===false)
            localStorage.setItem('infoList',JSON.stringify(state.infoList))
            state.searchList=state.infoList
        },
        searchInfolist(state,val){
            state.searchList=val
            // localStorage.setItem('infoList',JSON.stringify(state.infoList))
        }
    },
    actions:{
        deleteItem({commit},item){
            commit('deleteItem',item)
        },
        addNewItem({commit},item){
            commit('addNewItem',item)
        }
    },
    getters:{
        list(state){
            return state.infoList
        },
        showDialog(state){
            return state.showDialog
        },
        todo(state){
            return state.todo
        },
        searchList(state){
            return state.searchList
        }
    }
}