import { ref } from 'vue'
export default {
  name:'App',
  components:{Header,FooTer,LiSt,DiaLog},
  setup(){  
    
    const cancel=()=>{
      store.commit('list/changeShowDlg',false)
    }

    const store=useStore()

    const clearOne=ref(false)
    const createNewOne=()=>{
      clearOne.value=true
    }
    return {cancel,createNewOne,clearOne}
  }
};
</script>

